section#manage {
  padding-top: 30px;
}

.clock-manage {
  color: #00e868;
  font-size: 3em;
}

.countdown-time {
  max-width: 600px;
  margin: 0 auto;
  margin-top: 80px;

  display: flex;
  flex-flow: row;
  justify-content: center;
  flex-wrap: wrap;
}

.countdown-time button {
  margin: 10px;
}

.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 3px solid transparent;
  padding: .375rem .75rem;
  font-size: 1.5rem;
  line-height: 1.5;
  border-radius: 1.5rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.btn-green {
  color: #00e868;
  background-color: transparent;
  background-image: none;
  border-color: #00e868;
}

.btn-green:hover {
  color: #000;
  background-color: #00e868;
  border-color: #00e868;
}

.btn-green:active {
  background-color: #099447;
  border-color: #099447;
}

.btn-green:focus {
  outline: 0;
}

.btn:not(:disabled):not(.disabled) {
  cursor: pointer;
}

.countdown-time-custom {
  padding-top: 60px;

  text-align: center;
}

.countdown-time-custom .btn-plus, 
.countdown-time-custom .btn-minus,
.countdown-time-custom .input-time-custom {
  background: transparent;
  color: #00e868;
  border: none;
  font-size: 2rem;
  text-align: center;
}

.countdown-time-custom .btn-plus, 
.countdown-time-custom .btn-minus {
  cursor: pointer;
}

.countdown-time-custom .btn-plus:focus, 
.countdown-time-custom .btn-minus:focus {
  outline: 0;
}

.countdown-time-custom input {
  width: 140px;
}

.countdown-time-custom .btn-apply {
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  font-size: 1rem;
  border-width: 2px;
}